type RealmConfig = {
  maxCultivation: number;
  label: string;
};

type SpiritRootConfig = {
  label: string;
};

export enum Realm {
  QI_1 = "QI_1",
  QI_2 = "QI_2",
  QI_3 = "QI_3",
  QI_4 = "QI_4",
  QI_5 = "QI_5",
  QI_6 = "QI_6",
  QI_7 = "QI_7",
  QI_8 = "QI_8",
  QI_9 = "QI_9",
  QI_10 = "QI_10",
}

export enum SpiritRoot {
  FAKE = "FAKE",
  TRUE = "TRUE",
  HEAVEN = "HEAVEN",
  MUTANT = "MUTANT",
}

const orderedRealms: Realm[] = [
  Realm.QI_1,
  Realm.QI_2,
  Realm.QI_3,
  Realm.QI_4,
  Realm.QI_5,
  Realm.QI_6,
  Realm.QI_7,
  Realm.QI_8,
  Realm.QI_9,
  Realm.QI_10,
];

export const MAX_REALM = Realm.QI_10;

export function getNextRealm(realm: Realm): Realm | null {
  const index = orderedRealms.indexOf(realm);
  if (index === -1) {
    return null;
  }
  if (index >= orderedRealms.length - 1) {
    return null;
  }
  return orderedRealms[index + 1];
}

